import React, { useState } from 'react'
import { Row, Col, Card, CardHeader, CardBody, Form, Button, FormFeedback, Input, InputGroupText, InputGroupAddon, InputGroup, Alert } from 'reactstrap'
import { useInput, useSubmit } from "../../../hooks/form";
import { handleValidation, validations } from "../../../utils/validations";
import { AppSwitch } from '@coreui/react'
import { frontendRoutes, messages } from '../../../utils/constants';
import { apiPost } from '../../../utils/serviceManager';
import { backendRoutes } from '../../../utils/constants';
import useAlert from '../../../hooks/useAlert';

export default function CreateQAForm(props) {

	const adData = props.location.state ? props.location.state.data : { creative_id: '', questions: [] };
	const quesText = useInput("quesText", "", handleValidation, validations.EMPTY);
	const [answers, setAnswers] = useState([{ answer: null, correct: false }, { answer: null, correct: false }]);
	const [quesCount, setQuesCount] = useState(adData.questions.length)
	const alert = useAlert(false);

	const submit = useSubmit([quesText], handleSuccess);

	if (props.location.state === undefined) {
		props.history.push({
			pathname: frontendRoutes.dashboard.path,
		})
	}


	function handleAlertVisibility() {
		alert.changeVisibility(false);
	}

	function handleSuccess(formData) {
		formData = { question: formData.quesText, "answers": answers, "creative_id": adData.creative_id }
		let isSubmit = false

		answers.forEach((ans, i) => {
			if (!ans.answer) {
				alert.changeVisibility(true);
				alert.handleMessage("Please fill all the options to continue.");
				setTimeout(() => {
					alert.changeVisibility(false);
				}, 3000);
				isSubmit = false
				return
			}
			else {
				isSubmit = true
			}
		})

		if (isSubmit) {
			console.log("Data Length==> ", formData);

			callInsertQuestion(formData)
			quesText.clearInput();
			setAnswers([{ answer: null, correct: false }, { answer: null, correct: false }])
			// setAnswers((ans) => {
			// 	ans = ans.map((data, i) => {
			// 		return data.answer = ''
			// 	})
			// 	return ans
			// })
		}

	}

	function callInsertQuestion(formData) {
		apiPost(
			backendRoutes.INSERT_QUESTION,
			{
				"data": formData
			},
			{},
			successRes => {
				console.log("successRes", successRes)
				setQuesCount(quesCount + 1)
				alert.changeVisibility(true);
				alert.handleMessage(messages.QUESTION_CREATED);
				setTimeout(() => {
					alert.changeVisibility(false);
				}, 5000);
			},
			errorRes => {
				console.log("errorRes", errorRes)
				if (errorRes.response && errorRes.response.data) {
					alert.handleMessage(errorRes.response.data.message);
				} else {
					alert.handleMessage(messages.NETWORK_FAILURE);
				}
				setTimeout(() => {
					alert.changeVisibility(false);
				}, 5000);
			}
		)
	}


	function handleChange(i, event) {
		const values = [...answers];
		values[i].answer = event.target.value;
		setAnswers(values);
	}

	function handleSwitchChange(i, event) {
		const values = [...answers];
		values[i].correct = event.target['checked'];
		setAnswers(values);
	}

	function handleAdd() {
		const values = [...answers];
		if (values.length <= 4) {
			values.push({ answer: null, correct: false });
			setAnswers(values);
		}
		else {
			console.log("maximum upto 5 answers can be added.")
		}

	}

	function handleRemove(i) {
		const values = [...answers];

		values.splice(i, 1);
		setAnswers(values);
	}

	function renderAnswers() {
		return (
			answers.map((field, idx) => {
				return (
					<div className="row align-items-center justify-content-between" color='red' key={`${field}-${idx}`}>
						<div className="col-4">
							<InputGroup className="mb-3">
								<InputGroupAddon addonType="prepend">
									<InputGroupText>
										{/* <i className="icon-screen-desktop"></i> */}
										{/* <i> {idx + 1} </i> */}
										{idx + 1}
									</InputGroupText>
								</InputGroupAddon>
								<Input
									type="text"
									placeholder="Option"
									value={field.answer || ""}
									onChange={e => handleChange(idx, e)}
								/>
								{/* <FormFeedback>{optionText.helperText}</FormFeedback> */}
							</InputGroup>
						</div>

						<div className="col-3">
							<div className="mb-3 row align-items-center justify-content-start">
								Is Correct Option -
									<AppSwitch
									className={'mx-1'}
									color={'primary'}
									outline={'alt'}
									label
									checked={field.correct}
									dataOn={'\u2713'}
									dataOff={'\u2715'}
									onChange={e => handleSwitchChange(idx, e)}
								/>
							</div>
						</div>
						{
							idx > 1 ? (
								<div className="col-3">
									<Button onClick={() => handleRemove(idx)}>Delete</Button>
								</div>
							) : (
									<div className="col-3">
										{/* <Button onClick={() => handleRemove(idx)}>Delete</Button> */}
									</div>
								)
						}

					</div>
				)
			})

		)
	}

	// console.log("answers and values: ", answers)

	return (
		<div className="animated fadeIn">
			<Row>
				<Col xl="12">
					<Card>
						<CardHeader>
							{/* <i className="fa fa-align-justify"></i> Questions{" "}
							<small className="text-muted">creation form</small> */}
							<strong><i className="icon-info pr-1"></i></strong> Ad_id: {adData.creative_id}

						</CardHeader>
						<CardBody>
							<Alert
								color="danger"
								isOpen={alert.isVisible}
								toggle={handleAlertVisibility}
							>
								{alert.message}
							</Alert>
							{
								quesCount <= 9 ? (
									<Form noValidate {...submit.props}>
										<h1>Add Questions</h1>
										<p className="text-muted">{10 - quesCount} more questions can be added </p>
										<InputGroup className="mb-3">
											<InputGroupAddon addonType="prepend">
												<InputGroupText>
													<i className="icon-screen-desktop"></i>
												</InputGroupText>
											</InputGroupAddon>
											<Input
												type="text"
												placeholder="Question"
												{...quesText.props}
											/>
											<FormFeedback>{quesText.helperText}</FormFeedback>
										</InputGroup>

										{renderAnswers()}

										<Button color="primary" onClick={() => handleAdd()}>
											Add Option
								</Button>
										<p></p>
										<Button color="success" block>
											Create Question
                </Button>
									</Form>
								) : (
										<h1>No more questions can be added! Max limit is 10.</h1>
									)
							}

						</CardBody>
					</Card>
				</Col>
			</Row>
		</div>
	)
}

function Option({ getOptions, optionId, deleteOption }) {
	const optionText = useInput({ optionId }, "", handleValidation, validations.EMPTY);
	const [isCorrect, setIsCorrect] = useState(false)
	let optionData = {
		answer: optionText,
		correct: isCorrect,
		optionId: optionId
	}
	getOptions(optionData)
	function handleSwitchChange(e) {
		setIsCorrect(e.target['checked'])
	}
	return (
		// <Row xl="12">
		// 	<Col sm={{ size: 'auto', offset: 0 }}>
		// 		<InputGroup className="mb-3">
		// 			<InputGroupAddon addonType="prepend">
		// 				<InputGroupText>
		// 					<i className="icon-screen-desktop"></i>
		// 				</InputGroupText>
		// 			</InputGroupAddon>
		// 			<Input
		// 				type="text"
		// 				placeholder="Question"
		// 				{...optionText.props}
		// 			/>
		// 			<FormFeedback>{optionText.helperText}</FormFeedback>
		// 		</InputGroup>
		// 	</Col>
		// 	<Col sm={{ size: 'auto', offset: 2}}>
		// 		<Row>
		// 			Is Correct Option -
		// 		<AppSwitch className={'mx-1'} color={'primary'} outline={'alt'} label checked dataOn={'\u2713'} dataOff={'\u2715'} />
		// 		</Row>
		// 	</Col>
		// </Row>
		<div className="container-fluid">
			<div className="row align-items-center justify-content-between" color='red'>
				<div className="col-4">
					<InputGroup className="mb-3">
						<InputGroupAddon addonType="prepend">
							<InputGroupText>
								<i className="icon-screen-desktop"></i>
							</InputGroupText>
						</InputGroupAddon>
						<Input
							type="text"
							placeholder="Option"
							{...optionText.props}
						/>
						<FormFeedback>{optionText.helperText}</FormFeedback>
					</InputGroup>
				</div>

				<div className="col-3">
					<div className="mb-3 row align-items-center justify-content-start">
						Is Correct Option -
					<AppSwitch
							className={'mx-1'}
							color={'primary'}
							outline={'alt'}
							label
							checked={isCorrect}
							dataOn={'\u2713'}
							dataOff={'\u2715'}
							onChange={handleSwitchChange}
						/>
					</div>
				</div>
				<div className="col-3">
					{/* <div className="mb-3 row align-items-center justify-content-start">
					Is Correct Option -
					<AppSwitch 
						className={'mx-1'} 
						color={'primary'} 
						outline={'alt'} F
						label 
						checked={isCorrect}
						dataOn={'\u2713'} 
						dataOff={'\u2715'} 
						onChange = {handleSwitchChange}
					/>
					</div> */}
					<Button onClick={(e) => deleteOption(e, optionId)}>Delete</Button>
				</div>
			</div>
		</div>


	)
}
