import React, { useState } from 'react'
import { Row, Col, Card, CardHeader, CardBody, Form, Button, FormFeedback, Input, InputGroupText, InputGroupAddon, InputGroup, Alert } from 'reactstrap'
import { useInput, useSubmit } from "../../../hooks/form";
import { handleValidation, validations } from "../../../utils/validations";
import { AppSwitch } from '@coreui/react'
import { frontendRoutes, messages } from '../../../utils/constants';
import { apiPost } from '../../../utils/serviceManager';
import { backendRoutes } from '../../../utils/constants';
import useAlert from '../../../hooks/useAlert';

export default function CreateQAForm(props) {

	const quesData = props.location.state ? props.location.state.data : undefined

	if (!quesData) {
		props.history.push({
			pathname: frontendRoutes.dashboard.path,
		})
	}

	// console.log("Options array: ", quesData)

	const quesText = useInput("quesText", quesData.question, handleValidation, validations.EMPTY);
	const [answers, setAnswers] = useState(quesData.answers);
	const alert = useAlert(false);
	const submit = useSubmit([quesText], handleSuccess);

	function handleAlertVisibility() {
		alert.changeVisibility(false);
	}

	function handleSuccess(formData) {
		formData = { question: formData.quesText, "answers": answers, question_id: quesData.question_id }
		callUpdateQuestion(formData)
		console.log("Data Length==> ", formData);

	}

	function callUpdateQuestion(formData) {
		apiPost(
			backendRoutes.UPDATE_QUESTION,
			{
				"data": formData
			},
			{},
			successRes => {
				console.log("successRes", successRes)
				// setQuesCount(quesCount + 1)
				alert.changeVisibility(true);
				alert.handleMessage(messages.QUESTION_UPDATED);
				setTimeout(() => {
					alert.changeVisibility(false);
				}, 5000);
			},
			errorRes => {
				console.log("errorRes", errorRes)
				if (errorRes.response && errorRes.response.data) {
					alert.handleMessage(errorRes.response.data.message);
				} else {
					alert.handleMessage(messages.NETWORK_FAILURE);
				}
				setTimeout(() => {
					alert.changeVisibility(false);
				}, 5000);
			}
		)
	}

	function handleChange(i, event) {
		const values = [...answers];
		values[i].answer = event.target.value;
		setAnswers(values);
	}

	function handleSwitchChange(i, event) {
		const values = [...answers];
		values[i].correct = event.target['checked'];
		setAnswers(values);
	}

	function handleAdd() {
		const values = [...answers];
		if (values.length <= 4) {
			values.push({ answer: null, correct: false });
			setAnswers(values);
		}
		else {
			console.log("maximum upto 5 answers can be added.")
		}

	}

	function handleRemove(idx, event) {
		event.preventDefault()
		const values = [...answers];
		const ansId = values[idx].answer_id

		console.log("Deleted answer id: ", ansId)

		deleteAnswer(ansId, idx)

		// values.splice(i, 1);
		// setAnswers(values);
	}

	function deleteAnswer(answerId, idx) {
		apiPost(
			backendRoutes.DELETE_ANSWER,
			{
				'answer_id': answerId
			},
			{},
			successRes => {
				console.log("Answer deleted successfully: ", successRes)
				const values = [...answers];
				values.splice(idx, 1);
				setAnswers(values);
			},
			errorRes => {
				console.log("Answer deleted error: ", errorRes)
			}
		)
	}

	function renderAnswers() {
		return (
			answers.map((field, idx) => {
				return (
					<div className="row align-items-center justify-content-between" color='red' key={`${field}-${idx}`}>
						<div className="col-4">
							<InputGroup className="mb-3">
								<InputGroupAddon addonType="prepend">
									<InputGroupText>
										{/* <i className="icon-screen-desktop"></i> */}
										{/* <i> {idx + 1} </i> */}
										{idx + 1}
									</InputGroupText>
								</InputGroupAddon>
								<Input
									type="text"
									placeholder="Option"
									value={field.answer || ""}
									onChange={e => handleChange(idx, e)}
								/>
								{/* <FormFeedback>{optionText.helperText}</FormFeedback> */}
							</InputGroup>
						</div>

						<div className="col-3">
							<div className="mb-3 row align-items-center justify-content-start">
								Is Correct Option -
									<AppSwitch
									className={'mx-1'}
									color={'primary'}
									outline={'alt'}
									label
									checked={field.correct}
									dataOn={'\u2713'}
									dataOff={'\u2715'}
									onChange={e => handleSwitchChange(idx, e)}
								/>
							</div>
						</div>
						{
							idx > 1 ? (
								<div className="col-3">
									<Button onClick={(e) => handleRemove(idx, e)}>Delete</Button>
								</div>
							) : (
									<div className="col-3">

									</div>
								)
						}

					</div>
				)
			})

		)
	}

	// console.log("answers and values: ", answers)

	return (
		<div className="animated fadeIn">
			<Row>
				<Col xl="12">
					<Card>
						<CardHeader>
							{/* <i className="fa fa-align-justify"></i> Questions{" "}
						<small className="text-muted">creation form</small> */}
							<strong><i className="icon-info pr-1"></i></strong> Question id: {quesData.question_id}

						</CardHeader>
						<CardBody>
							<Alert
								color="danger"
								isOpen={alert.isVisible}
								toggle={handleAlertVisibility}
							>
								{alert.message}
							</Alert>

							<Form noValidate {...submit.props}>
								<h1>Edit Question</h1>
								{/* <p className="text-muted"></p> */}
								<InputGroup className="mb-3">
									<InputGroupAddon addonType="prepend">
										<InputGroupText>
											<i className="icon-screen-desktop"></i>
										</InputGroupText>
									</InputGroupAddon>
									<Input
										type="text"
										placeholder="Question"
										{...quesText.props}
									/>
									<FormFeedback>{quesText.helperText}</FormFeedback>
								</InputGroup>

								{renderAnswers()}

								{/* <Button color="primary" onClick={() => handleAdd()}>
									Add Option
								</Button> */}
								{/* <p></p> */}
								<Button color="success" block>
									Edit Question
								</Button>
							</Form>

						</CardBody>
					</Card>
				</Col>
			</Row>
		</div>
	)
}


